/* EmployeeSettings.css */
.employee-settings-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  animation: fadeIn 0.3s ease-in-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.employee-settings-container {
  background-color: white;
  border-radius: 12px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
  width: 90%;
  max-width: 600px;
  max-height: 90vh;
  overflow-y: auto;
  animation: slideUp 0.3s ease-in-out;
}

@keyframes slideUp {
  from {
    transform: translateY(50px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.employee-settings-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  border-bottom: 2px solid #e0e0e0;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border-radius: 12px 12px 0 0;
}

.employee-settings-header h2 {
  margin: 0;
  font-size: 1.5rem;
  font-weight: 600;
}

.employee-settings-close {
  background: none;
  border: none;
  font-size: 1.5rem;
  color: white;
  cursor: pointer;
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
  transition: background-color 0.2s;
}

.employee-settings-close:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

.employee-settings-form {
  padding: 1.5rem;
}

.employee-settings-section {
  margin-bottom: 2rem;
  padding-bottom: 1.5rem;
  border-bottom: 1px solid #e0e0e0;
}

.employee-settings-section:last-of-type {
  border-bottom: none;
}

.employee-settings-section h3 {
  margin: 0 0 0.5rem 0;
  font-size: 1.2rem;
  color: #333;
  font-weight: 600;
}

.employee-settings-hint {
  margin: 0 0 1rem 0;
  font-size: 0.9rem;
  color: #666;
  font-style: italic;
}

.employee-settings-field {
  margin-bottom: 1.25rem;
}

.employee-settings-field label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 500;
  color: #444;
  font-size: 0.95rem;
}

.employee-settings-field input,
.employee-settings-field textarea {
  width: 100%;
  padding: 0.75rem;
  border: 1.5px solid #ddd;
  border-radius: 6px;
  font-size: 1rem;
  transition: all 0.2s;
  font-family: inherit;
}

.employee-settings-field input:focus,
.employee-settings-field textarea:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.employee-settings-field input.error,
.employee-settings-field textarea.error {
  border-color: #dc3545;
}

.password-input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.password-input-wrapper input {
  flex: 1;
  padding-right: 3rem;
}

.password-toggle {
  position: absolute;
  right: 1rem;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 1.2rem;
  padding: 0.25rem 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: opacity 0.2s;
  z-index: 10;
}

.password-toggle:hover {
  opacity: 0.7;
}

.password-toggle:focus {
  outline: none;
}

/* SVG Eye Icon - Heroicons style */
.eye-icon {
  width: 20px;
  height: 20px;
  color: #64748b;
  stroke-width: 1.5;
}

.employee-settings-field textarea {
  resize: vertical;
  min-height: 80px;
}

.error-message {
  display: block;
  margin-top: 0.4rem;
  color: #dc3545;
  font-size: 0.85rem;
  font-weight: 500;
}

.employee-settings-success {
  padding: 1rem;
  background-color: #d4edda;
  color: #155724;
  border: 1px solid #c3e6cb;
  border-radius: 6px;
  margin-bottom: 1rem;
  font-weight: 500;
  text-align: center;
}

.employee-settings-error {
  padding: 1rem;
  background-color: #f8d7da;
  color: #721c24;
  border: 1px solid #f5c6cb;
  border-radius: 6px;
  margin-bottom: 1rem;
  font-weight: 500;
  text-align: center;
}

.employee-settings-actions {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  margin-top: 1.5rem;
  padding-top: 1rem;
  border-top: 1px solid #e0e0e0;
}

.employee-settings-cancel,
.employee-settings-submit {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 6px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.employee-settings-cancel {
  background-color: #f0f0f0;
  color: #333;
}

.employee-settings-cancel:hover {
  background-color: #e0e0e0;
}

.employee-settings-submit {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.employee-settings-submit:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.4);
}

.employee-settings-submit:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* Responsive Design */
@media (max-width: 768px) {
  .employee-settings-container {
    width: 95%;
    max-height: 95vh;
  }

  .employee-settings-header {
    padding: 1rem;
  }

  .employee-settings-header h2 {
    font-size: 1.25rem;
  }

  .employee-settings-form {
    padding: 1rem;
  }

  .employee-settings-actions {
    flex-direction: column-reverse;
  }

  .employee-settings-cancel,
  .employee-settings-submit {
    width: 100%;
  }
}
