.worklog-management-container {
  padding: 1rem;
  background: linear-gradient(135deg, #ffffff 0%, #f8fafc 100%);
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.08), 0 1px 4px rgba(0, 0, 0, 0.05);
  border: 1px solid rgba(226, 232, 240, 0.8);
  color: #1e293b;
  font-family: Arial, sans-serif;
}

.error-message {
  background: linear-gradient(135deg, #fef2f2 0%, #fee2e2 100%);
  border: 1px solid #fca5a5;
  color: #dc2626;
  padding: 1rem 1.5rem;
  border-radius: 12px;
  margin: 1rem 0;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-family: Arial, sans-serif;
}

.pending-title {
  color: #ff6600;
  font-weight: 700;
  font-size: 1.25rem;
  margin-bottom: 0.5rem;
}

.approved-title {
  color: #008000;
  font-weight: 700;
  font-size: 1.25rem;
  margin-bottom: 0.5rem;
}

.worklog-management-table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0;
  background: transparent;
  border: 1px solid rgba(226, 232, 240, 0.8);
  font-family: Arial, sans-serif;
  color: #475569;
}

/* Apply gradient and styles to each individual <th> */
.worklog-management-table thead th {
  background: linear-gradient(135deg, #f1f5f9 0%, #e2e8f0 100%);
  font-weight: 700;
  color: #1e293b;
  text-transform: uppercase;
  font-size: 0.85rem;
  letter-spacing: 0.05em;
  border-bottom: 2px solid #e2e8f0;
  padding: 1.5rem 2rem;
  position: relative;
  /* Rounded corners on the first and last th */
  /* border-top-left-radius: 16px; */
}

.worklog-management-table thead th:last-child {
  border-top-left-radius: 0;
  border-top-right-radius: 16px;
}

.worklog-management-table th,
.worklog-management-table td {
  border-bottom: 1px solid #f1f5f9;
  vertical-align: middle;
  font-weight: 500;
  font-size: 0.95rem;
  padding: 1.5rem 2rem;
}

.worklog-management-table tr:hover {
  background: linear-gradient(135deg, #f8fafc 0%, #f1f5f9 100%);
}

.worklog-management-table tr:last-child td {
  border-bottom: none;
}

.worklog-management-table tr:last-child td:first-child {
  border-bottom-left-radius: 16px;
}

.worklog-management-table tr:last-child td:last-child {
  border-bottom-right-radius: 16px;
}

.log-text-cell {
  max-width: 400px;
  word-wrap: break-word;
  color: #53619d;
  font-weight: 500;
}

.approve-button {
  padding: 0.5rem 1rem;
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
  color: #ffffff;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  font-size: 0.85rem;
  cursor: pointer;
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  margin: 0 0.25rem;
  min-width: 70px;
  box-shadow: 0 2px 8px rgba(16, 185, 129, 0.3);
}

.approve-button:hover {
  background: linear-gradient(135deg, #059669 0%, #047857 100%);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(16, 185, 129, 0.4);
}

.approve-button:disabled {
  background-color: #9ccc9c;
  cursor: not-allowed;
  box-shadow: none;
}

/* Converted inline styles from JSX */
.loading-message {
