.worklog-grid {
  padding: 1.5rem;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1.5rem;
  background: #f8fafc;
}

@media (max-width: 1400px) {
  .worklog-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 1024px) {
  .worklog-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 640px) {
  .worklog-grid {
    grid-template-columns: 1fr;
  }
}

