/* Dialog/Modal Styles */
.dialog-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.dialog-content {
  background: white;
  border-radius: 12px;
  padding: 2rem;
  max-width: 500px;
  width: 90%;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
}

.dialog-content h3 {
  margin: 0 0 1rem 0;
  color: #1e293b;
}

.dialog-content label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 600;
  color: #475569;
}

.dialog-content .required {
  color: #ef4444;
}

.dialog-content textarea {
  width: 100%;
  padding: 0.75rem;
  border: 2px solid #cbd5e1;
  border-radius: 8px;
  font-size: 0.95rem;
  font-family: inherit;
  resize: vertical;
  margin-bottom: 1rem;
}

.dialog-content textarea:focus {
  outline: none;
  border-color: #3b82f6;
}

.dialog-actions {
  display: flex;
  gap: 0.75rem;
  justify-content: flex-end;
}

.dialog-actions .btn {
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  font-weight: 600;
  font-size: 0.95rem;
  cursor: pointer;
  transition: all 0.2s;
  border: none;
}

.dialog-actions .btn-cancel {
  background: white;
  color: #64748b;
  border: 2px solid #cbd5e1;
}

.dialog-actions .btn-cancel:hover {
  background: #f1f5f9;
}

.dialog-actions .btn-approve {
  background: #10b981;
  color: white;
}

.dialog-actions .btn-approve:hover {
  background: #059669;
}

.dialog-actions .btn-reject {
  background: #ef4444;
  color: white;
}

.dialog-actions .btn-reject:hover {
  background: #dc2626;
}
