/* Employee Dashboard Title */
.employee-dashboard-title {
  font-size: 2rem;
  font-weight: 800;
  text-align: center;
  margin: 1rem 0 2rem 0;
  background: linear-gradient(135deg, #1e293b 0%, #3b82f6 50%, #10b981 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  letter-spacing: -0.02em;
}

.punch-container {
  max-width: 100%;
 
  padding: 1rem 1rem;
  background: linear-gradient(135deg, #ffffff 0%, #f8fafc 100%);
  border-radius: 20px;
  /* box-shadow: 0 20px 60px rgba(0, 0, 0, 0.08), 0 8px 32px rgba(0, 0, 0, 0.05); */
  border: 1px solid rgba(226, 232, 240, 0.8);
  font-family: 'Segoe UI', -apple-system, BlinkMacSystemFont, sans-serif;
  overflow: hidden;
  box-sizing: border-box;
}

.punch-container:hover {
 
  box-shadow: 0 25px 80px rgba(0, 0, 0, 0.12), 0 12px 40px rgba(0, 0, 0, 0.08);
}

.punch-header {
  text-align: center;
  margin-bottom: 2.5rem;
}

.punch-title {
  font-size: 2.25rem;
  font-weight: 800;
  margin-bottom: 1rem;
  background: linear-gradient(135deg, #1e293b 0%, #3b82f6 50%, #10b981 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  letter-spacing: -0.02em;
}

.current-time {
  font-size: 2.75rem;
  font-weight: 900;
  color: #0c0f15;
  text-align: center;
  margin: 2rem 0;
  font-family: 'Monaco', 'Menlo', monospace;
  text-shadow: 0 4px 8px rgba(59, 130, 246, 0.2);
  background: linear-gradient(135deg, #3b82f6 0%, #6366f1 100%);
  background-clip: text;
  /* -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; */
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(59, 130, 246, 0.05);
  background-clip: padding-box;
  -webkit-background-clip: padding-box;
  color: #050b14;
}

.punch-status {
  background: linear-gradient(135deg, #f1f5f9 0%, #e2e8f0 100%);
  padding: 2rem;
  border-radius: 16px;
  margin-bottom: 2.5rem;
  text-align: center;
  border: 1px solid rgba(226, 232, 240, 0.8);
  box-shadow: inset 0 2px 4px rgba(0, 0, 0, 0.02);
}

.status-text {
  font-size: 1.15rem;
  color: #1e293b;
  margin-bottom: 0.75rem;
  font-weight: 600;
}

.last-punch {
  color: #64748b;
  font-size: 0.95rem;
  font-weight: 500;
}

.punch-buttons {
  display: flex;
  gap: 1.25rem;
  margin-bottom: 1.5rem;
  justify-content: center;
}

.punch-in-button,
.punch-out-button {
  width: 200px;
  padding: 1rem 1.5rem;
  font-size: 1.1rem;
  font-weight: 700;
  border: none;
  border-radius: 14px;
  color: #ffffff;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.6rem;
  position: relative;
  overflow: hidden;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.punch-in-button {
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
  box-shadow: 0 4px 20px rgba(16, 185, 129, 0.3);
}

.punch-out-button {
  background: linear-gradient(135deg, #ef4444 0%, #dc2626 100%);
  box-shadow: 0 4px 20px rgba(239, 68, 68, 0.3);
}

.punch-in-button:before,
.punch-out-button:before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s;
}

.punch-in-button:hover:before,
.punch-out-button:hover:before {
  left: 100%;
}

.punch-in-button:hover {
  background: linear-gradient(135deg, #059669 0%, #047857 100%);
  transform: translateY(-2px);
  box-shadow: 0 8px 30px rgba(16, 185, 129, 0.4);
}

.punch-out-button:hover {
  background: linear-gradient(135deg, #dc2626 0%, #b91c1c 100%);
  transform: translateY(-2px);
  box-shadow: 0 8px 30px rgba(239, 68, 68, 0.4);
}

.punch-in-button:active,
.punch-out-button:active {
  transform: translateY(0);
}

.punch-in-button:disabled,
.punch-out-button:disabled {
  background: linear-gradient(135deg, #d1d5db 0%, #9ca3af 100%);
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.punch-in-button:disabled:before,
.punch-out-button:disabled:before {
  display: none;
}

/* Employee Tab Navigation */
.employee-tabs {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 2rem;
  border-bottom: 2px solid #e2e8f0;
  padding-bottom: 0;
  overflow-x: auto;
}

.employee-tab {
  padding: 1rem 1.5rem;
  background: transparent;
  border: none;
  border-bottom: 3px solid transparent;
  font-size: 1rem;
  font-weight: 600;
  color: #64748b;
  cursor: pointer;
  transition: all 0.3s ease;
  white-space: nowrap;
}

.employee-tab:hover {
  color: #3b82f6;
  background: rgba(59, 130, 246, 0.05);
}

.employee-tab.active {
  color: #3b82f6;
  border-bottom-color: #3b82f6;
  background: rgba(59, 130, 246, 0.08);
}

/* Employee Tab Content */
.employee-tab-content {
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
  box-sizing: border-box;
}

.attendance-history {
  margin-top: 2rem;
}

.history-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #1e293b;
  margin-bottom: 1.5rem;
  text-align: center;
  border-bottom: 2px solid #e2e8f0;
  padding-bottom: 0.75rem;
}

/* Table wrapper for responsive overflow */
.table-wrapper {
  width: 100%;
  overflow-x: auto;
  overflow-y: visible;
  margin: 1rem 0;
}

.history-table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0;
  background: linear-gradient(135deg, #ffffff 0%, #f8fafc 100%);
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.06);
  border: 1px solid rgba(226, 232, 240, 0.8);
  table-layout: auto;
  max-width: 100%;
}

.history-table th {
  padding: 1rem 1.5rem;
  text-align: left;
  background: linear-gradient(135deg, #f1f5f9 0%, #e2e8f0 100%);
  font-weight: 700;
  color: #1e293b;
  text-transform: uppercase;
  font-size: 0.85rem;
  letter-spacing: 0.05em;
  border-bottom: 2px solid #e2e8f0;
}

.history-table th.date-column {
  border-top-left-radius: 12px;
}

.history-table th.status-column {
  border-top-right-radius: 12px;
}

.history-table td {
  padding: 1rem 1.5rem;
  border-bottom: 1px solid #f1f5f9;
  color: #475569;
  font-size: 0.9rem;
  font-weight: 500;
}

.history-table tr:hover {
  background: linear-gradient(135deg, #f8fafc 0%, #f1f5f9 100%);
}

.history-table tr:last-child td {
  border-bottom: none;
}

.status-in {
  color: #10b981;
  font-weight: 700;
  background: rgba(16, 185, 129, 0.1);
  padding: 0.25rem 0.75rem;
  border-radius: 20px;
  font-size: 0.8rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.status-out {
  color: #ef4444;
  font-weight: 700;
  background: rgba(239, 68, 68, 0.1);
  padding: 0.25rem 0.75rem;
  border-radius: 20px;
  font-size: 0.8rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.no-records {
  text-align: center;
  padding: 3rem 2rem;
  color: #64748b;
  font-style: italic;
  font-size: 1.1rem;
  margin: 0;
}

.loading-spinner {
  text-align: center;
  color: #3b82f6;
  padding: 1.5rem;
  font-size: 1.1rem;
  font-weight: 600;
}

.error-message {
  background: linear-gradient(135deg, #fef2f2 0%, #fee2e2 100%);
  border: 1px solid #fca5a5;
  color: #dc2626;
  padding: 1rem 1.5rem;
  border-radius: 12px;
  margin: 1rem 0;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 0.75rem;
  text-align: center;
}

/* Responsive Design */
@media (max-width: 480px) {
  .punch-container {
    margin: 1rem;
    padding: 2rem 1.5rem;
  }

  .punch-title {
    font-size: 1.75rem;
  }

  .current-time {
    font-size: 2.25rem;
  }

  .punch-buttons {
    flex-direction: column;
    gap: 1rem;
  }

  .punch-in-button,
  .punch-out-button {
    padding: 1rem;
    font-size: 1rem;
  }

  .history-table th,
  .history-table td {
    padding: 0.75rem 1rem;
    font-size: 0.85rem;
  }
}

@media (max-width: 320px) {
  .history-table {
    font-size: 0.8rem;
  }

  .history-table th,
  .history-table td {
    padding: 0.5rem 0.75rem;
  }
}

/* Layout Classes - converted from inline styles */
.employee-layout-container {
  display: flex;
  gap: 2rem;
  min-height: 100vh;
  padding: 0.5rem;
  align-items: flex-start;
  max-width: 100%;
  overflow-x: hidden;
}

.employee-sidebar {
  width: 280px;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  min-width: 280px;
}

.employee-sidebar-form {
  background-color: #fff;
  padding: 1.5rem;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  max-height: calc(100vh - 150px);
  overflow-y: auto;
}

.punch-container-flex {
  flex: 1;
  min-width: 0;
  overflow-x: auto;
}

.punch-button-disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Control Bar Styles */
.controls-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  flex-wrap: wrap;
  gap: 12px;
}

.search-filter-container {
  display: flex;
  gap: 8px;
  align-items: center;
  flex-wrap: wrap;
}

.search-input {
  padding: 8px 12px;
  border: 1px solid #cbd5e0;
  border-radius: 8px;
  font-size: 14px;
  width: 250px;
  transition: all 0.3s ease;
}

.search-input:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.filter-select {
  padding: 8px 12px;
  border: 1px solid #cbd5e0;
  border-radius: 8px;
  font-size: 14px;
  background-color: white;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 140px;
}

.filter-select:hover {
  border-color: #3b82f6;
}

.filter-select:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.rows-label {
  font-size: 13px;
  font-weight: 600;
  padding: 0 8px;
}

.rows-select {
  padding: 6px 10px;
  border: 1px solid #cbd5e0;
  border-radius: 6px;
  font-size: 13px;
  cursor: pointer;
  background-color: white;
}

.rows-select:focus {
  outline: none;
  border-color: #3b82f6;
}

.action-buttons-group {
  display: flex;
  gap: 8px;
  align-items: center;
  flex-wrap: wrap;
}

.btn-show-all {
  padding: 8px 16px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 600;
  font-size: 13px;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px rgba(102, 126, 234, 0.3);
}

.btn-show-all:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.4);
}

.btn-print {
  padding: 8px 16px;
  background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 600;
  font-size: 13px;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px rgba(240, 147, 251, 0.3);
  display: flex;
  align-items: center;
  gap: 6px;
}

.btn-print:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(240, 147, 251, 0.4);
}

.btn-export {
  padding: 8px 16px;
  background: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 600;
  font-size: 13px;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px rgba(79, 172, 254, 0.3);
  display: flex;
  align-items: center;
  gap: 6px;
}

.btn-export:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(79, 172, 254, 0.4);
}

.table-empty-message {
  text-align: center;
  padding: 2rem;
  color: #6c757d;
  font-style: italic;
}

/* Pagination Styles */
.pagination-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
  margin-top: 1rem;
  flex-wrap: wrap;
}

.pagination-info {
  font-size: 13px;
  color: #64748b;
  font-weight: 500;
}

.pagination-buttons {
  display: flex;
  gap: 8px;
}

.pagination-button {
  padding: 6px 12px;
  border: 1px solid #cbd5e0;
  background-color: white;
  border-radius: 6px;
  cursor: pointer;
  font-size: 13px;
  font-weight: 600;
  transition: all 0.2s ease;
  color: #475569;
}

.pagination-button:hover:not(:disabled) {
  background-color: #3b82f6;
  color: white;
  border-color: #3b82f6;
}

.pagination-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  background-color: #f1f5f9;
}

.pagination-button.active {
  background-color: #3b82f6;
  color: white;
  border-color: #3b82f6;
}

/* Additional responsive layout fixes */
@media (max-width: 1200px) {
  .employee-layout-container {
    gap: 1.5rem;
  }
  
  .employee-sidebar {
    width: 240px;
  }
}

@media (max-width: 992px) {
  .employee-layout-container {
    flex-direction: column;
    gap: 1rem;
  }
  
  .employee-sidebar {
    width: 100%;
    max-width: 500px;
  }
  
  .punch-container-flex {
    width: 100%;
  }
}
