.employee-details-view {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #f5f5f5;
  z-index: 1000;
  overflow-y: auto;
  padding: 20px;
}

.employee-details-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: white;
  padding: 20px 30px;
  border-radius: 12px;
  margin-bottom: 20px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.employee-details-header h1 {
  margin: 0;
  font-size: 28px;
  color: #1a1a1a;
  font-weight: 600;
}

.btn-close-icon {
  background: #f44336;
  color: white;
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 20px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 0.3s, transform 0.1s;
}

.btn-close-icon:hover {
  background: #d32f2f;
  transform: scale(1.05);
}

.employee-details-loading,
.employee-details-error {
  background: white;
  padding: 40px;
  border-radius: 12px;
  text-align: center;
  font-size: 18px;
  color: #666;
}

.employee-details-error {
  color: #c33;
}

.employee-details-content {
  display: flex;
  flex-direction: column;
  gap: 20px;
  max-width: 1400px;
  margin: 0 auto;
}

.details-card {
  background: white;
  border-radius: 12px;
  padding: 24px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.details-card h2 {
  margin: 0 0 20px 0;
  font-size: 22px;
  color: #1a1a1a;
  font-weight: 600;
}

.details-card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  flex-wrap: wrap;
  gap: 16px;
}

.details-card-actions {
  display: flex;
  gap: 12px;
  align-items: center;
}

.search-input {
  padding: 8px 12px;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 14px;
  min-width: 200px;
}

.btn-action {
  padding: 8px 16px;
  background: #4CAF50;
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 14px;
  cursor: pointer;
  transition: background 0.3s;
}

.btn-action:hover {
  background: #45a049;
}

/* Profile Details Card */
.profile-details-grid {
  display: grid;
  grid-template-columns: 180px 1fr;
  gap: 30px;
  align-items: start;
}

.profile-details-avatar {
  width: 180px;
  height: 180px;
  border-radius: 12px;
  overflow: hidden;
  border: 4px solid #4CAF50;
  box-shadow: 0 4px 12px rgba(76, 175, 80, 0.2);
}

.profile-details-avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.profile-details-avatar-placeholder {
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #4CAF50, #45a049);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 64px;
  font-weight: 600;
  color: white;
  text-transform: uppercase;
}

.profile-details-info {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.profile-detail-row {
  font-size: 16px;
  line-height: 1.6;
  color: #333;
}

.profile-detail-row strong {
  color: #1a1a1a;
  margin-right: 8px;
}

/* Tables */
.table-container {
  overflow-x: auto;
  margin-bottom: 16px;
}

.details-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 14px;
}

.details-table th {
  background: #f5f5f5;
  padding: 12px;
  text-align: left;
  font-weight: 600;
  color: #1a1a1a;
  border-bottom: 2px solid #ddd;
}

.details-table td {
  padding: 12px;
  border-bottom: 1px solid #eee;
  color: #333;
}

.details-table tbody tr:hover {
  background: #f9f9f9;
}

.status-badge {
  padding: 4px 12px;
  border-radius: 12px;
  font-size: 12px;
  font-weight: 500;
  text-transform: uppercase;
}

.status-badge.approved {
  background: #e8f5e9;
  color: #2e7d32;
}

.status-badge.pending {
  background: #fff3e0;
  color: #e65100;
}

/* Pagination */
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 12px;
  padding-top: 12px;
}

.pagination button {
  padding: 8px 16px;
  background: #4CAF50;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
  transition: background 0.3s;
}

.pagination button:hover:not(:disabled) {
  background: #45a049;
}

.pagination button:disabled {
  background: #ccc;
  cursor: not-allowed;
}

.pagination span {
  font-size: 14px;
  color: #666;
}

@media (max-width: 768px) {
  .employee-details-view {
    padding: 12px;
  }

  .employee-details-header {
    padding: 16px;
  }

  .employee-details-header h1 {
    font-size: 20px;
  }

  .profile-details-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }

  .profile-details-avatar {
    width: 150px;
    height: 150px;
    margin: 0 auto;
  }

  .details-card-header {
    flex-direction: column;
    align-items: flex-start;
  }

  .details-card-actions {
    width: 100%;
    flex-direction: column;
  }

  .search-input {
    width: 100%;
  }
}
