.worklog-container {
  padding: 1rem;
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 8px;
}

.today-logs-count {
  margin-bottom: 1rem;
  color: #666;
  font-weight: bold;
}

.worklog-form {
  margin-bottom: 2rem;
}

.worklog-textarea {
  width: 100%;
  padding: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
  resize: vertical;
}

.worklog-submit-button {
  margin-top: 0.5rem;
  padding: 0.5rem 1rem;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.worklog-submit-button:disabled {
  background-color: #9ccc9c;
  cursor: not-allowed;
}

.worklog-error {
  color: red;
  margin-bottom: 1rem;
}

.worklog-success {
  color: green;
  margin-bottom: 1rem;
}

.worklog-table {
  width: 100%;
  border-collapse: collapse;
  border: 1px solid #53619d;
  color: #000801;
}

.worklog-table th, 
.worklog-table td {
  border: 1px solid #150202;
  padding: 8px;
  vertical-align: top;
}

/* Apply gradient background individually to each th */
.worklog-table thead th {
  background: linear-gradient(135deg, #f1f5f9 0%, #e2e8f0 100%);
}

.worklog-text-cell {
  max-width: 300px;
  word-wrap: break-word;
  color: #53619d;
}

.worklog-status.approved {
  color: green;
  font-weight: bold;
}

.worklog-status.pending {
  color: orange;
  font-weight: bold;
}

h4 {
  color: #5481e2;
}

/* Additional classes for WorkLog conversion */
.today-logs-limit-reached {
  background-color: #f8d7da;
  color: #721c24;
  padding: 0.75rem;
  border-radius: 8px;
  margin-bottom: 1rem;
  font-weight: 600;
}

.today-logs-under-limit {
  background-color: #d4edda;
  color: #155724;
  padding: 0.75rem;
  border-radius: 8px;
  margin-bottom: 1rem;
  font-weight: 600;
}

.submit-tasks-button {
  width: 100%;
  padding: 1rem;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  margin-top: 1rem;
  opacity: 1;
}

.submit-tasks-button-disabled {
  cursor: not-allowed;
  opacity: 0.6;
}

.submission-history-section {
  margin-top: 2rem;
}

.submission-history-table-wrapper {
  overflow-x: auto;
}

.submission-history-table {
  width: 100%;
  border-collapse: collapse;
  background-color: white;
}

.submission-history-header-row {
  background-color: #f8f9fa;
}

.submission-history-th {
  padding: 0.75rem;
  text-align: left;
  border-bottom: 2px solid #dee2e6;
}

.submission-history-row {
  border-bottom: 1px solid #dee2e6;
}

.submission-history-td {
  padding: 0.75rem;
}

.submission-status-badge {
  padding: 0.25rem 0.75rem;
  border-radius: 12px;
  font-size: 0.85rem;
  font-weight: 600;
}

.submission-status-badge-approved {
  background-color: #d4edda;
  color: #155724;
}

.submission-status-badge-pending {
  background-color: #fff3cd;
  color: #856404;
}

.no-tasks-message {
  text-align: center;
  color: #6c757d;
  padding: 2rem;
  background-color: #f8f9fa;
  border-radius: 8px;
}

.task-section-wrapper {
  border-radius: 8px;
  padding: 1rem;
  margin-bottom: 1rem;
}

.task-section-title {
  margin-top: 0;
}

.task-item-wrapper {
  background-color: white;
  border: 1px solid #dee2e6;
  border-radius: 8px;
  padding: 1rem;
  margin-bottom: 0.75rem;
}

.task-item-rejected {
  border: 2px solid #dc3545;
  background: linear-gradient(to right, #fff5f5 0%, white 100%);
  box-shadow: 0 0 10px rgba(220, 53, 69, 0.1);
}

.task-item-content {
  display: flex;
  align-items: start;
  gap: 0.75rem;
}

.task-checkbox {
  margin-top: 0.25rem;
  width: 1.25rem;
  height: 1.25rem;
  cursor: pointer;
}

.task-checkbox-disabled {
  cursor: not-allowed;
}

.task-item-details {
  flex: 1;
}

.task-item-header {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 0.5rem;
}

.task-item-title {
  font-size: 1.05rem;
}

.task-priority-high-badge {
  padding: 0.25rem 0.5rem;
  border-radius: 12px;
  font-size: 0.75rem;
  font-weight: 600;
  background-color: #fee;
  color: #d63031;
}

.task-rejection-count-badge {
  padding: 0.25rem 0.5rem;
  border-radius: 12px;
  font-size: 0.75rem;
  font-weight: 600;
  background-color: #fff3cd;
  color: #856404;
  border: 1px solid #ffeaa7;
}

.rejection-icon {
  font-size: 0.9em;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

.task-item-description {
  color: #6c757d;
  font-size: 0.95rem;
  margin-bottom: 0.5rem;
}

.task-item-meta {
  font-size: 0.85rem;
  color: #6c757d;
  margin-bottom: 0.75rem;
}

.task-admin-remarks-box {
  background-color: #f8d7da;
  border: 1px solid #f5c6cb;
  border-radius: 6px;
  padding: 0.75rem;
  margin-bottom: 0.75rem;
}

.task-admin-remarks-label {
  color: #721c24;
}

.task-admin-remarks-text {
  color: #721c24;
  margin-top: 0.25rem;
}

.task-remarks-label {
  font-size: 0.9rem;
  font-weight: 600;
  display: block;
  margin-bottom: 0.25rem;
}

.task-remarks-textarea {
  width: 100%;
  padding: 0.5rem;
  border-radius: 6px;
  border: 1px solid #dee2e6;
  font-size: 0.9rem;
  resize: vertical;
}

.task-employee-remarks-readonly {
  background-color: #e7f3ff;
  border: 1px solid #b3d9ff;
  border-radius: 6px;
  padding: 0.75rem;
  margin-top: 0.5rem;
}

.task-employee-remarks-readonly-label {
  color: #004085;
}

.task-employee-remarks-readonly-text {
  color: #004085;
  margin-top: 0.25rem;
}

/* Completed Task Actions */
.completed-task-wrapper {
  background: linear-gradient(to right, #e7f3ff 0%, white 100%);
}

.task-completed-indicator {
  width: 1.25rem;
  height: 1.25rem;
  background: #10b981;
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 0.9rem;
  flex-shrink: 0;
  margin-top: 0.25rem;
}

.completed-task-actions {
  margin-top: 1rem;
  padding-top: 1rem;
  border-top: 1px solid #d1e7ff;
  display: flex;
  align-items: center;
  gap: 1rem;
}

.btn-revoke-submission {
  padding: 0.5rem 1rem;
  background: white;
  color: #f59e0b;
  border: 2px solid #f59e0b;
  border-radius: 6px;
  font-weight: 600;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.2s;
}

.btn-revoke-submission:hover {
  background: #f59e0b;
  color: white;
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(245, 158, 11, 0.3);
}

.completed-task-notice {
  color: #64748b;
  font-style: italic;
}
