/* WorkLogHistory Container */
.worklog-history-container {
  padding: 1rem;
}

/* Header Section */
.worklog-history-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.worklog-history-title {
  margin: 0;
  color: #2c3e50;
}

.worklog-history-search {
  padding: 0.5rem 1rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 250px;
  font-size: 0.9rem;
}

/* Loading State */
.worklog-history-loading {
  text-align: center;
  padding: 2rem;
  color: #6c757d;
}

/* Table Wrapper & Table */
.worklog-history-table-wrapper {
  overflow-x: auto;
}

.worklog-history-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 1rem;
  min-width: 1400px;
}

/* Table Header */
.worklog-history-table-header-row {
  background-color: #e3f2fd;
  border-bottom: 2px solid #2196F3;
}

.worklog-history-th {
  padding: 0.5rem;
  text-align: left;
}

.worklog-history-th-sortable {
  padding: 0.5rem;
  text-align: left;
  cursor: pointer;
  user-select: none;
}

.worklog-history-th-date {
  min-width: 140px;
}

.worklog-history-th-task {
  min-width: 200px;
}

.worklog-history-th-due {
  min-width: 120px;
}

.worklog-history-th-priority {
  min-width: 100px;
}

.worklog-history-th-status {
  min-width: 100px;
}

.worklog-history-th-emp-remarks {
  min-width: 150px;
}

.worklog-history-th-approver {
  min-width: 120px;
}

.worklog-history-th-approval-time {
  min-width: 140px;
}

.worklog-history-th-admin-remarks {
  min-width: 150px;
}

.worklog-history-th-overdue {
  min-width: 100px;
}

/* Filter Row */
.worklog-history-filter-row {
  background-color: #f8f9fa;
}

.worklog-history-filter-cell {
  padding: 0.3rem;
}

.worklog-history-filter-select {
  width: 100%;
  padding: 0.25rem;
  font-size: 0.8rem;
  border: 1px solid #ddd;
  border-radius: 3px;
}

.worklog-history-filter-input {
  width: 100%;
  padding: 0.25rem;
  font-size: 0.8rem;
  border: 1px solid #ddd;
  border-radius: 3px;
}

.worklog-history-reset-button {
  width: 100%;
  padding: 0.25rem 0.5rem;
  font-size: 0.8rem;
  border: 1px solid #ef4444;
  border-radius: 4px;
  background-color: white;
  color: #ef4444;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.worklog-history-reset-button:hover {
  background-color: #ef4444;
  color: white;
}

/* Table Body */
.worklog-history-table-row {
  border-bottom: 1px solid #dee2e6;
}

.worklog-history-td {
  padding: 0.5rem;
  font-size: 0.85rem;
}

.worklog-history-td-ellipsis {
  max-width: 200px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/* Priority Badges */
.worklog-history-badge-priority {
  padding: 0.2rem 0.4rem;
  border-radius: 3px;
  font-size: 0.75rem;
  font-weight: 600;
}

.worklog-history-badge-priority-high {
  background-color: #ffebee;
  color: #c62828;
}

.worklog-history-badge-priority-medium {
  background-color: #fff3e0;
  color: #ef6c00;
}

.worklog-history-badge-priority-low {
  background-color: #e8f5e9;
  color: #2e7d32;
}

.worklog-history-badge-priority-na {
  background-color: #f0f0f0;
  color: #666;
}

/* Status Badges */
.worklog-history-badge-status {
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
  font-size: 0.8rem;
  font-weight: 500;
}

.worklog-history-badge-status-approved {
  background-color: #d4edda;
  color: #155724;
}

.worklog-history-badge-status-pending {
  background-color: #fff3cd;
  color: #856404;
}

.worklog-history-badge-status-rejected {
  background-color: #f8d7da;
  color: #721c24;
}

.worklog-history-badge-status-completed {
  background-color: #e3f2fd;
  color: #1976d2;
}

.worklog-history-badge-status-na {
  background-color: #f0f0f0;
  color: #666;
}

/* Overdue Badges */
.worklog-history-badge-overdue {
  padding: 0.2rem 0.4rem;
  border-radius: 3px;
  font-size: 0.75rem;
  font-weight: 600;
}

.worklog-history-badge-overdue-on-time {
  background-color: #e8f5e9;
  color: #2e7d32;
}

.worklog-history-badge-overdue-overdue {
  background-color: #ffebee;
  color: #c62828;
}

.worklog-history-badge-overdue-pending {
  background-color: #fff3e0;
  color: #ef6c00;
}

/* No Data Message */
.worklog-history-no-data {
  text-align: center;
  padding: 2rem;
  color: #6c757d;
  font-style: italic;
}

/* Bottom Controls */
.worklog-history-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 8px;
}

/* Pagination */
.worklog-history-pagination {
  display: flex;
  gap: 8px;
  align-items: center;
}

.worklog-history-page-button {
  padding: 0.4rem 0.8rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  background-color: white;
  cursor: pointer;
  font-size: 0.85rem;
}

.worklog-history-page-button-disabled {
  background-color: #f8f9fa;
  cursor: not-allowed;
}

.worklog-history-page-info {
  padding: 0 0.5rem;
  font-size: 0.9rem;
  color: #495057;
}

/* Actions Section */
.worklog-history-actions {
  display: flex;
  gap: 8px;
  align-items: center;
}

.worklog-history-date-filter {
  padding: 0.4rem 0.6rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 0.85rem;
  cursor: pointer;
}

.worklog-history-items-per-page {
  padding: 0.4rem 0.6rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 0.85rem;
  cursor: pointer;
}

.worklog-history-action-button {
  padding: 0.4rem 0.8rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  background-color: white;
  cursor: pointer;
  font-size: 0.85rem;
}

.worklog-history-action-button:hover {
  background-color: #f8f9fa;
}
