.face-capture-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.85);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10000;
  animation: fadeIn 0.3s ease;
}

.face-capture-modal {
  background: white;
  border-radius: 12px;
  padding: 32px;
  max-width: 720px;
  max-height: 95vh;
  overflow-y: auto;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.3);
  animation: slideUp 0.3s ease;
  position: relative;
}

.close-button {
  position: absolute;
  top: 16px;
  right: 16px;
  background: transparent;
  border: none;
  font-size: 28px;
  color: #dc3545;
  cursor: pointer;
  padding: 4px 8px;
  line-height: 1;
  transition: all 0.2s ease;
  border-radius: 4px;
  z-index: 10;
}

.close-button:hover {
  background: rgba(220, 53, 69, 0.1);
  transform: scale(1.1);
}

.face-capture-modal h2 {
  margin: 0 0 8px 0;
  color: #1a1a1a;
  font-size: 28px;
  font-weight: 600;
  text-align: center;
}

.user-name {
  text-align: center;
  color: #666;
  font-size: 14px;
  margin-bottom: 16px;
  font-weight: 500;
}

.instructions {
  background: #f0f7ff;
  border-left: 4px solid #3498db;
  padding: 12px 16px;
  margin: 16px 0;
  border-radius: 4px;
}

.instructions p {
  margin: 4px 0;
  color: #2c3e50;
  font-size: 14px;
}

.video-container {
  position: relative;
  width: 640px;
  height: 480px;
  margin: 20px auto;
  border-radius: 12px;
  overflow: hidden;
  border: 3px solid #3498db;
  box-shadow: 0 4px 12px rgba(52, 152, 219, 0.3);
}

.video-container video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.detection-canvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.face-status {
  position: absolute;
  bottom: 16px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(0, 0, 0, 0.75);
  padding: 12px 24px;
  border-radius: 24px;
  font-weight: 600;
  font-size: 16px;
}

.status-detected {
  color: #2ecc71;
}

.status-not-detected {
  color: #f39c12;
}

.action-buttons {
  display: flex;
  gap: 12px;
  justify-content: center;
  margin-top: 24px;
}

.btn-capture {
  padding: 14px 32px;
  font-size: 16px;
  font-weight: 600;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 200px;
}

.btn-capture.active {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.4);
}

.btn-capture.active:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(102, 126, 234, 0.5);
}

.btn-capture.active:active {
  transform: translateY(0);
}

.btn-capture.disabled {
  background: #95a5a6;
  color: #fff;
  cursor: not-allowed;
  opacity: 0.6;
}

.btn-cancel,
.btn-cancel-alt {
  padding: 14px 32px;
  font-size: 16px;
  font-weight: 500;
  background: transparent;
  color: #7f8c8d;
  border: 2px solid #bdc3c7;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.btn-cancel:hover,
.btn-cancel-alt:hover {
  background: #ecf0f1;
  border-color: #95a5a6;
  color: #2c3e50;
}

.loading-models {
  text-align: center;
  padding: 40px;
}

.loading-models h3 {
  color: #3498db;
  font-size: 24px;
  margin-bottom: 16px;
}

.loading-models p {
  color: #7f8c8d;
  font-size: 16px;
}

.error-message {
  text-align: center;
  padding: 40px;
}

.error-message h3 {
  color: #e74c3c;
  font-size: 24px;
  margin-bottom: 16px;
}

.error-message p {
  color: #7f8c8d;
  font-size: 16px;
  margin-bottom: 24px;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes slideUp {
  from { transform: translateY(20px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}

@media (max-width: 768px) {
  .face-capture-modal {
    padding: 16px;
    width: 95%;
  }
  
  .video-container {
    width: 100%;
    height: auto;
    aspect-ratio: 4/3;
  }
  
  .btn-capture,
  .btn-cancel-alt {
    min-width: auto;
    flex: 1;
  }
}
