/* App.css - Simplified Enterprise Style */

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #dde7ef; /* Light neutral bg */
  color: #2c3e50; /* Dark text for readability */
  margin: 0px;
  padding: 0px;
  user-select: text !important;
  -webkit-user-select: text !important;
  -moz-user-select: text !important;
  -ms-user-select: text !important;
}

/* Enable text selection everywhere */
*, *::before, *::after {
  user-select: text !important;
  -webkit-user-select: text !important;
  -moz-user-select: text !important;
}

/* Tables should be selectable */
table, thead, tbody, tr, th, td {
  user-select: text !important;
  -webkit-user-select: text !important;
}

/* Buttons should not be selectable */
button, .btn, input[type="submit"], input[type="button"] {
  user-select: none !important;
  -webkit-user-select: none !important;
}

/* CRITICAL: Force all input fields to be interactive at all times */
input, textarea, select {
  pointer-events: auto !important;
  cursor: text !important;
  user-select: text !important;
  -webkit-user-select: text !important;
  -moz-user-select: text !important;
  -ms-user-select: text !important;
}

input:not([readonly]):not([disabled]),
textarea:not([readonly]):not([disabled]),
select:not([readonly]):not([disabled]) {
  background-color: #ffffff !important;
  cursor: text !important;
}

input[type="file"] {
  cursor: pointer !important;
}

/* Ensure modals and overlays don't block inputs */
.profile-form-overlay,
.profile-form-modal {
  pointer-events: auto !important;
}

.profile-form-overlay * {
  pointer-events: auto !important;
}

/* Container for the entire app */
.app-container {
  max-width: 1800px;
  width: 100%;
  margin-top: 0px !important;
  margin-left: auto;
  margin-right: auto;
  background-color: #f6f8fb; /* White background for contrast */
  border-radius: 8px; /* Rounded corners for clean look */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05); /* Light shadow for depth */
  box-sizing: border-box;
  overflow-x: hidden;
  padding-left: 40px; /* Added left padding for better spacing */
  padding-right: 40px; /* Added right padding for better spacing */
}

/* App header / title bar */
.app-header {
  text-align: center;
  font-size: 2.2rem;
  font-weight: 700;
  margin-bottom: 2rem;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 50%, #f093fb 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  letter-spacing: 1px;
  text-transform: uppercase;
}

/* Dashboard Title / Subheading */
.dashboard h2 {
  font-size: 1.75rem;
  font-weight: 600;
  margin-bottom: 1rem;
  color: #2c3e50;
  border-bottom: 2px solid #3498db;
  padding-bottom: 0.5rem;
}

/* Buttons common style */
.btn {
  display: inline-block;
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 6px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 1rem;
}

/* Primary Button */
.btn-primary {
  background-color: #3498db;
  color: #fff;
}
.btn-primary:hover {
  background-color: #2980b9;
  box-shadow: 0 4px 8px rgba(41, 128, 185, 0.2);
}

/* Success Button */
.btn-success {
  background-color: #2ecc71;
  color: #fff;
}
.btn-success:hover {
  background-color: #27ae60;
  box-shadow: 0 4px 8px rgba(39, 174, 96, 0.2);
}

/* Danger Button */
.btn-danger {
  background-color: #e74c3c;
  color: #fff;
}
.btn-danger:hover {
  background-color: #c0392b;
  box-shadow: 0 4px 8px rgba(192, 57, 43, 0.2);
}

/* Forms and Input Fields */
.form-input {
  width: 100%;
  padding: 0.75rem 1rem;
  border: 2px solid #e9ecef;
  border-radius: 6px;
  font-size: 1rem;
  transition: border-color 0.3s, box-shadow 0.3s;
  background-color: #fff;
  color: #2c3e50;
  pointer-events: auto !important;
  user-select: text !important;
  -webkit-user-select: text !important;
  -moz-user-select: text !important;
  -ms-user-select: text !important;
}
.form-input:focus {
  border-color: #3498db;
  outline: none;
  box-shadow: 0 0 0 3px rgba(52, 152, 219, 0.2);
}
.form-input::placeholder {
  color: #95a5a6;
}

/* Cards for content grouping */
.card {
  background: #fff;
  border-radius: 8px;
  box-shadow: var(--shadow-md);
  padding: 1.5rem;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}
.card:hover {
  transform: translateY(-3px);
  box-shadow: var(--shadow-lg);
}

/* Error / Success Messages */
.error-message {
  color: #e74c3c;
  background-color: rgba(231, 76, 60, 0.1);
  border: 1px solid #e74c3c;
  padding: 0.75rem 1rem;
  border-radius: 6px;
  font-size: 0.9rem;
  display: flex;
  align-items: center;
  gap: 0.75rem;
}
.success-message {
  color: #2ecc71;
  background-color: rgba(46, 204, 113, 0.1);
  border: 1px solid #2ecc71;
  padding: 0.75rem 1rem;
  border-radius: 6px;
  font-size: 0.9rem;
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

/* Loading Spinner / Indicator */
.loading {
  text-align: center;
  font-size: 1.1rem;
  margin: 1rem 0;
  color: #3498db;
}

/* Responsive styles */
@media(max-width: 768px) {
  body {
    padding: 1rem;
  }
  .app-container {
    padding: 1rem;
  }
  .dashboard h2 {
    font-size: 1.5rem;
  }
}

/* Sortable table headers */
.sortable-header {
  cursor: pointer;
  user-select: none !important;
  -webkit-user-select: none !important;
  transition: background-color 0.2s ease;
}

.sortable-header:hover {
  background-color: rgba(52, 152, 219, 0.1);
}

.sort-indicator {
  margin-left: 0.5rem;
  font-size: 0.8rem;
  color: #7f8c8d;
  opacity: 0.6;
  transition: opacity 0.2s ease;
}

.sort-indicator.active {
  color: #3498db;
  opacity: 1;
  font-weight: bold;
}

.sortable-header:hover .sort-indicator {
  opacity: 1;
}
