
.employee-management {
  padding: 2rem;
  max-width: 1400px;
  margin: 0 auto;
  font-family: 'Segoe UI', -apple-system, BlinkMacSystemFont, sans-serif;
}

.employee-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2.5rem;
  padding: 2rem 2.5rem;
  background: linear-gradient(135deg, #ffffff 0%, #f8fafc 100%);
  border-radius: 16px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.08), 0 1px 4px rgba(0, 0, 0, 0.05);
  border: 1px solid rgba(226, 232, 240, 0.8);
}

.employee-title {
  font-size: 2.5rem;
  font-weight: 700;
  margin: 0;
  background: linear-gradient(135deg, #1e293b 0%, #10b981 50%, #06b6d4 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  letter-spacing: -0.02em;
}

.employee-form {
  background: linear-gradient(135deg, #ffffff 0%, #f8fafc 100%);
  padding: 2.5rem;
  border-radius: 16px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.08), 0 1px 4px rgba(0, 0, 0, 0.05);
  border: 1px solid rgba(226, 232, 240, 0.8);
  margin-bottom: 2.5rem;
}

.form-group {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  align-items: end;
  margin-bottom: 2rem;
}

.form-input,
.form-select {
  padding: 1rem 1.25rem;
  border: 2px solid #e2e8f0;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 500;
  background: #ffffff;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  color: #1e293b;
  min-height: 48px;
}

.form-input:focus,
.form-select:focus {
  border-color: #10b981;
  box-shadow: 0 0 0 4px rgba(16, 185, 129, 0.1), 0 4px 12px rgba(0, 0, 0, 0.05);
  outline: none;
  transform: translateY(-1px);
}

.form-input:hover,
.form-select:hover {
  border-color: #cbd5e1;
}

.form-input::placeholder {
  color: #94a3b8;
  font-weight: 400;
}

.form-select {
  cursor: pointer;
  appearance: none;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='none' viewBox='0 0 20 20'%3e%3cpath stroke='%236b7280' stroke-linecap='round' stroke-linejoin='round' stroke-width='1.5' d='m6 8 4 4 4-4'/%3e%3c/svg%3e");
  background-position: right 12px center;
  background-repeat: no-repeat;
  background-size: 16px;
  padding-right: 3rem;
}

.add-employee-button {
  padding: 1rem 2rem;
  border: none;
  border-radius: 12px;
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
  color: #ffffff;
  font-weight: 600;
  font-size: 0.95rem;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 4px 14px rgba(16, 185, 129, 0.3);
  position: relative;
  overflow: hidden;
  min-height: 48px;
  min-width: 140px;
}

.add-employee-button:before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s;
}

.add-employee-button:hover:before {
  left: 100%;
}

.add-employee-button:hover {
  background: linear-gradient(135deg, #059669 0%, #047857 100%);
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(16, 185, 129, 0.4);
}

.add-employee-button:disabled {
  background: linear-gradient(135deg, #d1d5db 0%, #9ca3af 100%);
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.add-employee-button:disabled:before {
  display: none;
}

.employee-list {
  background: linear-gradient(135deg, #ffffff 0%, #f8fafc 100%);
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.08), 0 1px 4px rgba(0, 0, 0, 0.05);
  border: 1px solid rgba(226, 232, 240, 0.8);
  margin-bottom: 2rem;
}

.employee-table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0;
  background: transparent;
}

.employee-table th {
  padding: 1.5rem 2rem;
  text-align: left;
  background: linear-gradient(135deg, #f1f5f9 0%, #e2e8f0 100%);
  font-weight: 700;
  color: #1e293b;
  text-transform: uppercase;
  font-size: 0.85rem;
  letter-spacing: 0.05em;
  border-bottom: 2px solid #e2e8f0;
}

.employee-table th:first-child {
  border-top-left-radius: 16px;
}

.employee-table th:last-child {
  border-top-right-radius: 16px;
}

.employee-table td {
  padding: 1.25rem 2rem;
  border-bottom: 1px solid #f1f5f9;
  color: #475569;
  font-size: 0.95rem;
  font-weight: 500;
  vertical-align: middle;
}

.employee-table tr:hover {
  background: linear-gradient(135deg, #f8fafc 0%, #f1f5f9 100%);
}

.employee-table tr:last-child td {
  border-bottom: none;
}

.employee-actions {
  display: flex;
  gap: 0.75rem;
  align-items: center;
}

.edit-button,
.delete-button {
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  font-size: 0.85rem;
  cursor: pointer;
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  min-width: 70px;
}

.edit-button {
  background: linear-gradient(135deg, #3b82f6 0%, #2563eb 100%);
  color: #ffffff;
  box-shadow: 0 2px 8px rgba(59, 130, 246, 0.3);
}

.edit-button:hover {
  background: linear-gradient(135deg, #2563eb 0%, #1d4ed8 100%);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.4);
}

.edit-button:disabled {
  background: linear-gradient(135deg, #d1d5db 0%, #9ca3af 100%);
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.delete-button {
  background: linear-gradient(135deg, #ef4444 0%, #dc2626 100%);
  color: #ffffff;
  box-shadow: 0 2px 8px rgba(239, 68, 68, 0.3);
}

.delete-button:hover {
  background: linear-gradient(135deg, #dc2626 0%, #b91c1c 100%);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(239, 68, 68, 0.4);
}

/* Inline editing styles */
.employee-table input[type="text"],
.employee-table input[type="email"],
.employee-table select {
  padding: 0.5rem 0.75rem;
  border: 2px solid #e2e8f0;
  border-radius: 6px;
  background: #ffffff;
  font-size: 0.9rem;
  font-weight: 500;
  color: #1e293b;
  transition: all 0.2s ease;
  width: 100%;
  min-width: 120px;
}

.employee-table input:focus,
.employee-table select:focus {
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
  outline: none;
}

.employee-table input[type="checkbox"] {
  width: 18px;
  height: 18px;
  cursor: pointer;
  accent-color: #10b981;
}

/* Approval status styling */
.employee-table td:nth-child(4) {
  font-weight: 600;
}

.employee-table tr td:nth-child(4):contains("Yes") {
  color: #10b981;
}

.employee-table tr td:nth-child(4):contains("No") {
  color: #ef4444;
}

/* Section headers */
h3 {
  font-size: 1.5rem;
  font-weight: 700;
  color: #1e293b;
  margin: 2.5rem 0 1rem 0;
  border-bottom: 2px solid #e2e8f0;
  padding-bottom: 0.5rem;
}

/* Empty state */
.employee-list p {
  text-align: center;
  padding: 3rem 2rem;
  color: #64748b;
  font-style: italic;
  font-size: 1.1rem;
  margin: 0;
}

/* Error handling */
.error-message {
  background: linear-gradient(135deg, #fef2f2 0%, #fee2e2 100%);
  border: 1px solid #fca5a5;
  color: #dc2626;
  padding: 1rem 1.5rem;
  border-radius: 12px;
  margin: 1rem 0;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .employee-management {
    padding: 1rem;
  }
  
  .employee-header {
    padding: 1.5rem;
    flex-direction: column;
    gap: 1rem;
    text-align: center;
  }
  
  .employee-title {
    font-size: 2rem;
  }
  
  .employee-form {
    padding: 1.5rem;
  }
  
  .form-group {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 768px) {
  .employee-list {
    overflow-x: auto;
  }
  
  .employee-table {
    min-width: 900px;
  }
  
  .employee-title {
    font-size: 1.75rem;
  }
  
  .employee-table th,
  .employee-table td {
    padding: 1rem 1.5rem;
  }
  
  .employee-actions {
    flex-direction: column;
    gap: 0.5rem;
  }
}

/* Error Message */
.employee-error-message {
  color: red;
}

/* Form Field Container */
.employee-form-field {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  min-height: 78px;
}

/* Input Error State */
.employee-input-error {
  border-color: #ef4444 !important;
}

/* Field Error Message */
.employee-field-error {
  color: #ef4444;
  font-size: 0.875rem;
  min-height: 1.25rem;
  display: block;
}

/* Search Wrapper */
.employee-search-wrapper {
  margin-bottom: 1rem;
  display: flex;
  gap: 0.5rem;
  align-items: center;
}

.employee-search-input {
  max-width: 500px;
  padding: 0.75rem 1rem;
  border: 2px solid #e2e8f0;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 500;
}

.employee-reset-button {
  padding: 0.75rem 1rem;
  border: 1px solid #ef4444;
  border-radius: 8px;
  background-color: white;
  color: #ef4444;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.employee-reset-button:hover {
  background-color: #ef4444;
  color: white;
}

/* Email Input Wrapper */
.email-input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.email-input-wrapper input {
  flex: 1;
  padding-right: 150px;
}

.email-domain-suffix {
  position: absolute;
  right: 1.5rem;
  color: #64748b;
  font-size: 1rem;
  font-weight: 500;
  pointer-events: none;
  user-select: none;
}

.password-input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.password-input-wrapper input {
  flex: 1;
  padding-right: 3rem;
}

.password-toggle-btn {
  position: absolute;
  right: 0.75rem;
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  cursor: pointer;
  padding: 0.25rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: opacity 0.2s;
  z-index: 10;
}

.password-toggle {
  position: absolute;
  right: 1rem;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 1.2rem;
  padding: 0.25rem 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: opacity 0.2s;
  z-index: 10;
}

.password-toggle:hover {
  opacity: 0.7;
}

.password-toggle:focus,
.password-toggle-btn:focus {
  outline: none;
}

/* SVG Eye Icon - Heroicons style */
.eye-icon {
  width: 20px;
  height: 20px;
  color: #64748b;
  stroke-width: 1.5;
}

/* Employee Name Link */
.employee-name-link {
  color: #2196F3;
  cursor: pointer;
  text-decoration: underline;
  font-weight: 500;
}

.employee-name-link:hover {
  color: #1976D2;
}

/* Pagination Controls */
.employee-pagination-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1.5rem;
  padding: 1rem;
  background-color: #f8fafc;
  border-radius: 8px;
}

.employee-pagination-buttons {
  display: flex;
  gap: 0.5rem;
  align-items: center;
  flex-wrap: wrap;
}

.employee-pagination-button {
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 6px;
  background-color: #3b82f6;
  color: white;
  cursor: pointer;
  font-weight: 600;
}

.employee-pagination-button-disabled {
  background-color: #e2e8f0;
  color: #94a3b8;
  cursor: not-allowed;
}

.employee-pagination-info {
  margin: 0 1rem;
  font-weight: 600;
}

.employee-items-per-page-select {
  margin-left: 1rem;
  padding: 0.5rem;
  border: 1px solid #cbd5e1;
  border-radius: 6px;
  background-color: white;
  cursor: pointer;
  font-weight: 600;
}

/* Action Buttons */
.employee-action-buttons {
  display: flex;
  gap: 0.5rem;
}

.employee-export-button {
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 6px;
  background-color: #10b981;
  color: white;
  cursor: pointer;
  font-weight: 600;
}

.employee-print-button {
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 6px;
  background-color: #6366f1;
  color: white;
  cursor: pointer;
  font-weight: 600;
}
