.profile-card {
  width: 100%;
  max-width: 280px;
  background: white;
  border-radius: 12px;
  padding: 24px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  height: fit-content;
  position: sticky;
  top: 20px;
  box-sizing: border-box;
  overflow: hidden;
}

.profile-card-header {
  text-align: center;
  padding-bottom: 20px;
  border-bottom: 1px solid #e0e0e0;
}

.profile-card-avatar {
  width: 120px;
  height: 120px;
  margin: 0 auto 16px;
  border-radius: 50%;
  overflow: hidden;
  border: 4px solid #4CAF50;
  box-shadow: 0 4px 12px rgba(76, 175, 80, 0.2);
}

.profile-card-avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.profile-card-avatar-placeholder {
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #4CAF50, #45a049);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 40px;
  font-weight: 600;
  color: white;
  text-transform: uppercase;
}

.profile-card-name {
  margin: 0;
  font-size: 20px;
  font-weight: 600;
  color: #1a1a1a;
  word-break: break-word;
}

.profile-card-info {
  display: flex;
  flex-direction: column;
  gap: 16px;
  padding-top: 20px;
}

.profile-card-info-item {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.profile-card-label {
  font-size: 12px;
  font-weight: 500;
  color: #666;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.profile-card-value {
  font-size: 14px;
  color: #1a1a1a;
  word-break: break-word;
}

.profile-card-address {
  line-height: 1.5;
  font-size: 13px;
}

@media (max-width: 1024px) {
  .profile-card {
    width: 100%;
    position: static;
    margin-bottom: 20px;
  }
}
