/* Shared Table Pagination Styles */

.table-pagination-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1.5rem;
  padding: 1rem;
  background-color: #f8fafc;
  border-radius: 8px;
  flex-wrap: wrap;
  gap: 1rem;
}

.table-pagination-controls {
  display: flex;
  gap: 0.5rem;
  align-items: center;
  flex-wrap: wrap;
}

.table-pagination-button {
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 6px;
  background-color: #3b82f6;
  color: white;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s ease;
}

.table-pagination-button:hover:not(:disabled) {
  background-color: #2563eb;
  transform: translateY(-1px);
}

.table-pagination-button:disabled {
  background-color: #e2e8f0;
  color: #94a3b8;
  cursor: not-allowed;
}

.table-pagination-info {
  margin: 0 1rem;
  font-weight: 600;
  color: #1e293b;
}

.table-pagination-select {
  margin-left: 1rem;
  padding: 0.5rem;
  border: 1px solid #cbd5e1;
  border-radius: 6px;
  background-color: white;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s ease;
}

.table-pagination-select:hover {
  border-color: #3b82f6;
}

.table-pagination-select:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.table-reset-button {
  margin-left: 1rem;
  padding: 0.5rem 1rem;
  border: 1px solid #ef4444;
  border-radius: 6px;
  background-color: white;
  color: #ef4444;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.table-reset-button:hover {
  background-color: #ef4444;
  color: white;
}

.table-pagination-actions {
  display: flex;
  gap: 0.5rem;
}

.table-export-button {
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 6px;
  background-color: #10b981;
  color: white;
  cursor: pointer;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  transition: all 0.3s ease;
}

.table-export-button:hover {
  background-color: #059669;
  transform: translateY(-1px);
}

.table-print-button {
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 6px;
  background-color: #6366f1;
  color: white;
  cursor: pointer;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  transition: all 0.3s ease;
}

.table-print-button:hover {
  background-color: #4f46e5;
  transform: translateY(-1px);
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .table-pagination-container {
    flex-direction: column;
    align-items: stretch;
  }

  .table-pagination-controls {
    justify-content: center;
  }

  .table-pagination-actions {
    justify-content: center;
  }
}
