.profile-form-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10000;
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.profile-form-modal {
  background: white;
  border-radius: 12px;
  padding: 32px;
  width: 90%;
  max-width: 600px;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.3);
  animation: slideUp 0.3s ease;
  position: relative;
}

.profile-form-cancel {
  position: absolute;
  top: 16px;
  right: 16px;
  background: transparent;
  border: none;
  font-size: 28px;
  color: #dc3545;
  cursor: pointer;
  padding: 4px 8px;
  line-height: 1;
  transition: all 0.2s ease;
  border-radius: 4px;
  z-index: 10;
}

.profile-form-cancel:hover {
  background: rgba(220, 53, 69, 0.1);
  transform: scale(1.1);
}

.profile-form-cancel:active {
  transform: scale(0.95);
}

@keyframes slideUp {
  from { transform: translateY(20px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}

.profile-form-modal h2 {
  margin: 0 0 8px 0;
  color: #1a1a1a;
  font-size: 28px;
  font-weight: 600;
}

.profile-form-subtitle {
  margin: 0 0 24px 0;
  color: #666;
  font-size: 14px;
}

.profile-form-error {
  background: #fee;
  color: #c33;
  padding: 12px;
  border-radius: 6px;
  margin-bottom: 20px;
  border: 1px solid #fcc;
}

.profile-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.profile-form-section {
  margin-bottom: 8px;
}

.profile-pic-label {
  display: block;
  font-weight: 500;
  color: #333;
  margin-bottom: 12px;
}

.profile-pic-upload {
  position: relative;
  width: 150px;
  height: 150px;
  margin: 0 auto;
  cursor: pointer;
  border-radius: 50%;
  overflow: hidden;
  border: 3px solid #e0e0e0;
  transition: border-color 0.3s;
}

.profile-pic-upload:hover {
  border-color: #4CAF50;
}

.profile-pic-preview {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.profile-pic-placeholder {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: #f5f5f5;
  color: #999;
  font-size: 14px;
}

.profile-pic-placeholder span:first-child {
  font-size: 40px;
  margin-bottom: 8px;
}

.profile-pic-input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}

.profile-form-row {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 16px;
}

.profile-form-field {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.profile-form-field label {
  font-weight: 500;
  color: #333;
  font-size: 14px;
}

.profile-form-field input,
.profile-form-field textarea {
  padding: 10px 12px;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 14px;
  transition: border-color 0.3s, box-shadow 0.3s;
  pointer-events: auto !important;
  user-select: text !important;
  -webkit-user-select: text !important;
  cursor: text;
  background: #ffffff;
  color: #333;
}

.profile-form-field input:focus,
.profile-form-field textarea:focus {
  outline: none;
  border-color: #4CAF50;
  box-shadow: 0 0 0 3px rgba(76, 175, 80, 0.1);
}

.profile-form-field textarea {
  resize: vertical;
  font-family: inherit;
}

.profile-form-submit {
  margin-top: 12px;
  padding: 14px 24px;
  background: #4CAF50;
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.3s, transform 0.1s;
}

.profile-form-submit:hover {
  background: #45a049;
}

.profile-form-submit:active {
  transform: scale(0.98);
}

@media (max-width: 768px) {
  .profile-form-row {
    grid-template-columns: 1fr;
  }
  
  .profile-form-modal {
    padding: 24px;
  }
}
