.register-container {
  max-width: 420px;
  margin: 2rem auto;
  padding: 3rem 2.5rem;
  background: linear-gradient(135deg, #ffffff 0%, #f8fafc 100%);
  border-radius: 20px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.08), 0 8px 32px rgba(0, 0, 0, 0.05);
  border: 1px solid rgba(226, 232, 240, 0.8);
  font-family: 'Segoe UI', -apple-system, BlinkMacSystemFont, sans-serif;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

.register-container:hover {
  transform: translateY(-5px);
  box-shadow: 0 25px 80px rgba(0, 0, 0, 0.12), 0 12px 40px rgba(0, 0, 0, 0.08);
}

.register-header {
  text-align: center;
  margin-bottom: 2.5rem;
}

.register-title {
  font-size: 2.5rem;
  font-weight: 800;
  margin-bottom: 0.5rem;
  background: linear-gradient(135deg, #1e293b 0%, #10b981 50%, #3b82f6 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  letter-spacing: -0.02em;
}

.register-subtitle {
  color: #64748b;
  font-size: 1rem;
  font-weight: 500;
  margin: 0;
}

.register-form {
  display: flex;
  flex-direction: column;
  gap: 1.75rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.form-label {
  color: #1e293b;
  font-weight: 600;
  font-size: 0.95rem;
  margin-bottom: 0.25rem;
}

.form-input {
  padding: 1.25rem 1.5rem;
  border: 2px solid #e2e8f0;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 500;
  background: #ffffff;
  color: #1e293b;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  pointer-events: auto !important;
  user-select: text !important;
  -webkit-user-select: text !important;
  cursor: text;
}

.form-input:focus {
  border-color: #10b981;
  outline: none;
  box-shadow: 0 0 0 4px rgba(16, 185, 129, 0.1), 0 4px 12px rgba(0, 0, 0, 0.05);
  transform: translateY(-1px);
}

.form-input:hover {
  border-color: #cbd5e1;
}

.form-input.input-error {
  border-color: #ef4444 !important;
  background: #fef2f2;
}

.error-text {
  display: block;
  color: #ef4444;
  font-size: 0.85rem;
  margin-top: 0.5rem;
  font-weight: 500;
}

/* Email Input Wrapper */
.email-input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.email-input-wrapper .form-input {
  flex: 1;
  padding-right: 150px;
}

.email-domain-suffix {
  position: absolute;
  right: 1.5rem;
  color: #64748b;
  font-size: 1rem;
  font-weight: 500;
  pointer-events: none;
  user-select: none;
}

.password-input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.password-input-wrapper .form-input {
  padding-right: 3rem;
}

.password-toggle {
  position: absolute;
  right: 1rem;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 1.2rem;
  padding: 0.25rem 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: opacity 0.2s;
  z-index: 10;
}

.password-toggle:hover {
  opacity: 0.7;
}

.password-toggle:focus {
  outline: none;
}

/* SVG Eye Icon - Heroicons style */
.eye-icon {
  width: 20px;
  height: 20px;
  color: #64748b;
  stroke-width: 1.5;
}

.password-strength {
  margin-top: 0.75rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.strength-bar {
  height: 4px;
  background: #e5e7eb;
  border-radius: 2px;
  overflow: hidden;
}

.strength-fill {
  height: 100%;
  transition: all 0.3s ease;
  border-radius: 2px;
}

.form-input::placeholder {
  color: #94a3b8;
  font-weight: 400;
}

.register-button {
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
  color: #ffffff;
  padding: 1.25rem;
  border: none;
  border-radius: 12px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  margin-top: 1rem;
  box-shadow: 0 4px 20px rgba(16, 185, 129, 0.3);
  position: relative;
  overflow: hidden;
}

.register-button:before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s;
}

.register-button:hover:before {
  left: 100%;
}

.register-button:hover {
  background: linear-gradient(135deg, #059669 0%, #047857 100%);
  transform: translateY(-2px);
  box-shadow: 0 8px 30px rgba(16, 185, 129, 0.4);
}

.register-button:active {
  transform: translateY(0);
  box-shadow: 0 4px 20px rgba(16, 185, 129, 0.3);
}

.register-button:disabled {
  background: linear-gradient(135deg, #d1d5db 0%, #9ca3af 100%);
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.register-button:disabled:before {
  display: none;
}

.error-message {
  color: #ef4444;
  font-size: 0.9rem;
  font-weight: 500;
  margin-top: 0.5rem;
  padding: 0.75rem 1rem;
  background: #fef2f2;
  border-radius: 8px;
  border-left: 4px solid #ef4444;
}

.success-message {
  color: #10b981;
  font-size: 0.9rem;
  font-weight: 500;
  margin-top: 0.5rem;
  padding: 0.75rem 1rem;
  background: #f0fdf4;
  border-radius: 8px;
  border-left: 4px solid #10b981;
}

.login-link {
  text-align: center;
  margin-top: 2rem;
  color: #64748b;
  font-size: 0.95rem;
  font-weight: 500;
}

.login-link-button {
  background: none;
  border: none;
  color: #10b981;
  font-weight: 600;
  cursor: pointer;
  padding: 0.25rem 0.5rem;
  margin-left: 0.25rem;
  transition: all 0.2s ease;
  border-radius: 4px;
}

.login-link-button:hover {
  color: #059669;
  background: rgba(16, 185, 129, 0.1);
  text-decoration: underline;
}

/* Responsive Design */
@media (max-width: 480px) {
  .register-container {
    margin: 1rem;
    padding: 2rem 1.5rem;
  }

  .register-title {
    font-size: 2rem;
  }

  .form-input {
    padding: 1rem 1.25rem;
  }

  .register-button {
    padding: 1rem;
    font-size: 1rem;
  }
}

/* Animation for form validation */
.form-input.error {
  border-color: #ef4444;
  background: #fef2f2;
  animation: shake 0.3s ease-in-out;
}

@keyframes shake {
  0%, 100% { transform: translateX(0); }
  25% { transform: translateX(-5px); }
  75% { transform: translateX(5px); }
}

/* Loading state */
.register-button.loading {
  pointer-events: none;
  opacity: 0.7;
}

.register-button.loading:after {
  content: '';
  position: absolute;
  width: 20px;
  height: 20px;
  margin: auto;
  border: 2px solid transparent;
  border-top-color: #ffffff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
